import React from "react";
import { RED, GREEN, BLUE } from "../constants";

/*
  utils
*/
const getR = color => {
  switch (color) {
    case RED:
      return 255;
    default:
      return 0;
  }
};
const getG = color => {
  switch (color) {
    case GREEN:
      return 255;
    default:
      return 0;
  }
};
const getB = color => {
  switch (color) {
    case BLUE:
      return 255;
    default:
      return 0;
  }
};

const getA = isOn => (isOn ? 1 : 0.1);

/*
  component
*/
const Light = ({ color, isOn }) => {
  const r = getR(color);
  const g = getG(color);
  const b = getB(color);
  const a = getA(isOn);

  return (
    <div
      className="light"
      style={{ backgroundColor: `rgba(${r}, ${g}, ${b}, ${a})` }}
    />
  );
};

export default Light;
